<template lang="pug">
    .notes
        .container
            .notes_top 
                h2.notes_top-title {{notes.length > 0 ? words.infobar[lang] : words.noinfobar[lang]}}
                button.notes_top-btn(@click="grid = !grid")
                    img(v-if="grid" src="@/assets/images/menu.png", alt="") 
                    img(v-else src="@/assets/images/grid.png", alt="") 
                    span {{grid ? words.list[lang] : words.grid[lang]}}
            .notes_list(:class="{active: !grid}")
              Noteitem(:grid='grid' v-for="note in notes" :key="note.id" :note="note" @delNote="$emit('delNote', note.id)" @changeNote="$emit('changeNote', note.id)" :lang="lang" :words="words")
</template>

<script>
import Noteitem from "@/components/Noteitem.vue";
export default {
  components: {
        Noteitem,
    },
    props:{
      notes: Array,
      words: Object,
      lang: String,
    },
  data() {
    return {
      grid: true,
    };
  },
};
</script>
