<template lang="pug">
    Navbar(@searchValue="search = $event" :lang="lang" @changeLang="changeLang" :words="words")
    Notes(:notes="filterNotes" @delNote="delNote" @changeNote="changeNote" :lang="lang" :words="words")
    Modal(v-show="modalOpen" @closeModal="closeModal" :currentId="currentId" @addNote="addNote" :edit="edit" :editNote="editNote" @editeNote="editeNote" :lang="lang" :words="words")
    AddButton(@openModal="openModal")
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Notes from "@/components/Notes.vue";
import Modal from "@/components/Modal.vue";
import AddButton from "@/components/AddButton.vue";
import langs from '@/lang';

export default {
  components: {
    Navbar,
    Notes,
    Modal,
    AddButton,
  },
  data() {
    return {
      modalOpen: false,
      notes: [],
      currentId: 1,
      edit: false,
      editNote: {},
      search: "",
      lang: 'ru',
      words: langs,
    };
  },

  created() {
    this.getNotes();
    this.getLang();
  },

  computed: {
    filterNotes() {
      return this.search
        ? this.notes.filter((note) =>
            note.title.toLowerCase().includes(this.search.toLowerCase())
          )
        : this.notes;
    },
  },

  methods: {
    openModal() {
      this.modalOpen = true;
    },
    closeModal(status) {
      this.modalOpen = status;
      setTimeout(() => (this.edit = false), 100);
    },
    addNote(item) {
      this.notes.push(item);
      this.modalOpen = false;
    },
    delNote(id) {
      let index = this.notes.findIndex((note) => note.id == id);
      this.notes.splice(index, 1);
    },
    getNotes() {
      const localNotes = localStorage.getItem("notes");
      if (localNotes) {
        try {
          this.notes = JSON.parse(localNotes);
        } catch (error) {
          console.error("ошибка при получении заметок из localStorage", error);
          this.notes = [];
        }
      }
    },
    changeNote(id) {
      this.edit = this.modalOpen = true;
      let pickedNote = this.notes.find((note) => note.id == id);
      this.editNote = pickedNote;
    },
    editeNote(noteEdited) {
      this.notes.forEach((note) => {
        if (note.id === noteEdited.id) {
          note.title = noteEdited.title;
          note.text = noteEdited.text;
          note.date = noteEdited.date;
        }
      });
    },
    changeLang(val){
      this.lang = val;
      localStorage.setItem('lang', val);
    },
    getLang(){
      const storeLang = localStorage.getItem('lang');
      if (storeLang) {
        this.lang = storeLang;
      } else {
        localStorage.setItem('lang', this.lang);
      }
    }
  },

  watch: {
    notes: {
      handler(newNotes) {
        localStorage.notes = JSON.stringify(this.notes);
      },
      deep: true,
    },
  },
};
</script>
