<template lang="pug">
    header.header 
        Transition(name='header_content') 
            .header_content(v-show='header===true')
                button.header_content-lang(v-if="lang == 'ru'" @click="changeLang") RU
                button.header_content-lang(v-else @click="changeLang") UZ
                h1.header_content-title {{words.appbartitle[lang]}}
                button.header_content-search(@click='header = false')
                    img(src="@/assets/images/search.png" alt="search")
        Transition(name='header_form') 
            .header_form(v-show='header===false')
                button.header_form-back(@click="(header = true), (search = '')")
                    img(src="@/assets/images/back.png" alt="back")
                input.header_form-search(type='text', :placeholder='words.appbarsearch[lang]' v-model='search')
                button.header_form-clear(@click="search = ''")
                    img(src="@/assets/images/close.png" alt="close")
</template>

<script>
export default {
  data() {
    return {
      header: true,
      search: "",
    };
  },
  props: {
    lang: String,
    words: Object, 
  },
  watch: {
    search(val) {
      this.$emit("searchValue", val);
    },
  },
  methods: {
    changeLang() {
      this.$emit("changeLang", this.lang == "ru" ? "uz" : "ru");
    },
  },
};
</script>
