<template lang="pug">
    Transition(name="modal")
        .modal(@click="closeModal")
            .modal_block(@click.stop)
                h2.modal_block-title {{edit ? words.titlewindowedit[lang] : words.titlewindow[lang]}}
                .modal_inputs
                    label
                        span Title
                        input(type="text" placeholder="Title" v-model="title")
                    label
                        span Content
                        textarea(placeholder="Content" v-model="text")
                .modal_btns
                    button.cancel(@click="closeModal") {{words.closebtn[lang]}}
                    button.add(@click="addNote" v-if="!edit") {{words.addbtn[lang]}}
                    button.add(@click="changeNote" v-else) {{words.editwindowbtn[lang]}}
</template>

<script>
export default {
  data() {
    return {
      title: "",
      text: "",
      id: this.currentId,
    };
  },
  props: {
    currentId: Number,
    edit: Boolean,
    editNote: Object,
    words: Object,
    lang: String,
  },
  methods: {
    closeModal() {
      this.$emit("closeModal", false);
    },
    addNote() {
      if (this.title != "" && this.text != "") {
        const item = {
          id: this.id++,
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("addNote", item);
        this.title = "";
        this.text = "";
      }
    },
    changeNote(){
      if (this.title != "" && this.text != "") {
        const editeNote = {
          id: this.editNote.id,
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("editeNote", editeNote);
        this.closeModal();
      }
    }
  },
};
</script>