<template lang="pug">
    .notes_item
        .notes_item-top(:class="{between: !grid}")
            h4.notes_item-title {{note.title}}
            p.notes_item-date {{note.date}}
        p.notes_item-text
            | {{note.text}}
        .notes_item-btns 
            button.notes_item-btn.edit(@click="$emit('changeNote', note.id)")
                img(src="@/assets/images/list.png")
                span {{words.editbtn[lang]}}
            button.notes_item-btn.dell(@click="$emit('delNote', note.id)")
                img(src="@/assets/images/dell.png")
                span() {{words.deledit[lang]}}
</template>

<script>
export default {
  props: {
    grid: Boolean,
    note: Object,
    words: Object,
    lang: String,
  },
};
</script>
